import React from 'react'
import Link from 'gatsby-link'
import logo from '../../assets/hess-logo-white.png'

export default () => (
  <div className="logo">
    <a href="http://hess.com">
      <img src={logo} alt="Logo" />
    </a>
  </div>
)



